<template>
  <div id="new-group">
    <Layout>
      <Content>
        <Form ref="form" :model="formItem" :rules="rule">
          <FormItem label="id(id)">
            <i-Input disabled v-model="formItem.id"></i-Input>
          </FormItem>
          <FormItem label="" prop="name">
            <i-Input
              v-model="formItem.name"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem label="(" prop="password">
            <i-Input
              v-model="formItem.password"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem label=" " prop="member_can_use">
            <Tooltip max-width="300" content="">
              <Icon type="ios-help-circle" />
            </Tooltip>
            <i-switch v-model="formItem.member_can_use" />
          </FormItem>
          <FormItem label="">
            <i-Input
              type="textarea"
              :rows="5"
              v-model="formItem.desc"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem label="">
            <i-Input disabled v-model="formItem.owner"></i-Input>
          </FormItem>
          <FormItem label="">
            <DatePicker
              disabled
              type="datetime"
              format="yyyy-MM-dd HH:mm"
              v-model="formItem.create_time"
            ></DatePicker>
          </FormItem>
          <FormItem>
            <Poptip
              trigger="hover"
              content=""
              v-if="!formItem.is_owner"
              style="margin-right: 1%"
            >
              <Button
                type="primary"
                @click="submit()"
                :loading="loading"
                disabled
                ></Button
              >
            </Poptip>

            <Button
              v-else
              type="primary"
              @click="submit()"
              :loading="loading"
              style="margin-right: 1%"
              ></Button
            >

            <Poptip
              trigger="hover"
              content=""
              v-if="!formItem.is_owner"
            >
              <Button type="error" @click="del()" disabled></Button>
            </Poptip>

            <Button v-else type="error" @click="del()"></Button>
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
export default {
  name: "New",
  data() {
    return {
      id: this.$route.params.id,
      loading: false,
      formItem: {},
      rule: {
        name: [{ required: true, message: "", trigger: "change,blur" }],
        subject: [
          { required: true, message: "", trigger: "change,blur" },
        ],
        password: [
          { required: true, message: "", trigger: "change,blur" },
        ],
      },
    };
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.get();
    },
  },
  created() {
    this.get();
  },
  methods: {
    get() {
      this.$api.get(
        "group/",
        {
          id: this.id,
        },
        (response) => {
          if (response.status == 200) {
            var data = response.data;
            if (data.err_code == 0) {
              data = data.data;
              this.formItem = data;
            } else {
              this.$Message.error(data.error);
            }
          } else {
            this.$Message.error("" + response.data.err_code);
          }
        }
      );
    },
    del() {
      this.$api.delete(
        "group/",
        {
          id: this.formItem.id,
        },
        (response) => {
          if (response.status != 200) {
            this.$Message.error("");
            this.$Message.error("" + response);
          } else {
            var data = response.data;
            if (data.err_code != 0) {
              this.$Message.error("" + data.error);
            } else {
              this.$Message.success("");
              this.$router.go(-1);
            }
          }
        }
      );
    },
    submit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$api.put(
            "group/",
            {
              id: this.formItem.id,
              name: this.formItem.name,
              desc: this.formItem.desc,
              password: this.formItem.password,
              member_can_user:this.formItem.member_can_user
            },
            (response) => {
              if (response.status != 200) {
                this.loading = false;
                this.$Message.error("");
                this.$Message.error("" + response);
              } else {
                var data = response.data;
                if (data.err_code != 0) {
                  this.$Message.error("" + data.error);
                  this.loading = false;
                } else {
                  this.$Message.success("");
                  this.loading = false;
                }
              }
            }
          );
        } else {
          this.$Message.error("");
        }
      });
    },
  },
};
</script>

