<template>
  <div>
    <!--  -->
    <Modal v-model="show" @on-cancel="cancel" :mask-closable="false">
      <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
        <FormItem prop="nickname" style="display:inline">
          <i-Input type="text" v-model="formInline.nickname" placeholder="">
            <Icon type="md-person" slot="prepend"></Icon>
          </i-Input>
        </FormItem>
        <FormItem prop="user" style="display:inline">
          <i-Input type="text" v-model="formInline.user" placeholder="">
            <Icon type="ios-person-outline" slot="prepend"></Icon>
          </i-Input>
        </FormItem>
        <FormItem prop="password" style="display:inline">
          <i-Input type="password" v-model="formInline.password" placeholder="">
            <Icon type="ios-lock-outline" slot="prepend"></Icon>
          </i-Input>
        </FormItem>
        <FormItem prop="repassword" style="display:inline">
          <i-Input type="password" v-model="formInline.repassword" placeholder="">
            <Icon type="ios-lock-outline" slot="prepend"></Icon>
          </i-Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" size="large" @click="cancel"></Button>
        <Button type="primary" size="large" :loading="loading" @click="ok"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import crypto from 'crypto'
export default {
  name: "LoginModal",
  props: ["show_parent"],
  data() {
    return {
      show: this.show_parent,
      loading: false,
      error_msg: "",
      formInline: {
        user: "",
        email: "",
        password: "",
        nickname: "",
        repassword: ""
      },
      ruleInline: {//
        user: [
          { required: true, message: "", trigger: "change,blur" },
          {
            validator: (rule, value, callback) => {
              var reg = new RegExp("^[A-Z0-9]{5,15}$");
              if (!reg.test(value)) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "5-15",
            trigger: "change,blur"
          }
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              var reg = new RegExp(
                "^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$"
              );
              if (!reg.test(value)) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "6-18",
            trigger: "change,blur"
          }
        ],
        nickname: [
          { required: true, message: "", trigger: "change,blur" },
          {
            validator: (rule, value, callback) => {
              var reg = new RegExp("^[\u4e00-\u9fa5]{2,18}$");
              if (!reg.test(value)) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "2-18''",
            trigger: "change,blur"
          }
        ],
        repassword: [
          {
            validator: (rule, value, callback) => {
              if (value != this.formInline.password) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "",
            trigger: "change,blur"
          }
        ]
      }
    };
  },
  methods: {
    ok() {//
      this.$refs["formInline"].validate(valid => {
        if (valid) {
          this.loading = true;
          this.$api.post(
            "register/",
            {
              username: this.formInline.user,
              password: this.setMd5(this.formInline.password),
              nickname: this.formInline.nickname,
              email: this.formInline.email
            },
            response => {
              this.loading = false;
              var params = { registered: true };
              if (response.status != 200) {
                this.$Message.error("");
                console.log(response);
                this.$Message.error("" + response);
              } else {
                var data = response.data;
                if(data.err_code==0)
                {
                  data=data.data
                  if (data.result) {
                  this.show = false;
                  this.$Message.success("");
                  this.$emit("registerClosed", params);
                } else {
                  this.$Message.error(data.error);
                }
                }
                else{
                  this.$Message.error(""+data.error);
                }
              }
            }
          );
        } else {
          this.$Message.error("");
          this.$refs.modal.buttonLoading = false;
        }
      });
    },
    cancel() {//
      var params = { registered: false };
      this.show = false;
      this.$emit("registerClosed", params);
    },
    setMd5(text) {//
      var md5 = crypto.createHash("md5");
      md5.update(text); 
      var text_md5 = md5.digest("hex"); 
      return text_md5;
    }
  }
};
</script>
