<template>
  <div id="new">
    <Layout>
      <Content>
        <Form ref="form" :model="formItem" :rules="rule">
          <FormItem label="" prop="name">
            <i-Input v-model="formItem.name" placeholder=""></i-Input>
          </FormItem>
          <FormItem label="">
            <Select v-model="formItem.type">
              <Option value="file"></Option>
              <!-- <Option value="hypertext"></Option> -->
            </Select>
          </FormItem>
          <FormItem label="" prop="subject">
            <i-Input
              v-model="formItem.subject"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem label="">
            <i-Input
              type="textarea"
              :rows="5"
              v-model="formItem.remark"
              placeholder=""
            ></i-Input>
          </FormItem>
          <!-- <FormItem label="">
            <Select v-model="formItem.member_can_know_donelist">
              <Option value="false"></Option>
              <Option value="true"></Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Select v-model="formItem.member_can_see_others">
              <Option value="false"></Option>
              <Option value="true"></Option>
            </Select>
          </FormItem> -->
          <FormItem label="">
            <DatePicker
              type="datetime"
              format="yyyy-MM-dd HH:mm"
              placeholder=""
              v-model="formItem.end_time"
            ></DatePicker>
          </FormItem>
          <FormItem label="" prop="groups">
            <Tooltip
              max-width="300"
              content=""
            >
              <Icon type="ios-help-circle" />
            </Tooltip>
            <CheckboxGroup
              v-model="formItem.groups"
              @on-change="change_check()"
            >
              <List item-layout="vertical">
                <ListItem v-for="item in list_data" :key="item.id">
                  <Checkbox :label="item.id">
                    <span>{{ item.name }}</span>
                  </Checkbox>
                </ListItem>
              </List>
            </CheckboxGroup>

            <div style="text-align: center">
              <Page
                :current="page"
                :total="total"
                :page-size="page_size"
                @on-change="change_page"
              />
            </div>
          </FormItem>

          <FormItem>
            <Button type="primary" @click="submit()" :loading="loading"
              ></Button
            >
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
export default {
  name: "New",
  data() {
    return {
      page_size: 10,
      total: 0,
      list_data: "",
      page: 1,
      loading: false,
      formItem: {
        name: "",
        type: "file",
        subject: "",
        remark: "",
        end_time: new Date(),
        member_can_know_donelist: "false",
        member_can_see_others: "false",
        groups: [],
      },
      rule: {
        name: [{ required: true, message: "", trigger: "change,blur" }],
        subject: [
          { required: true, message: "", trigger: "change,blur" },
        ],
        groups:[{
          validator: (rule, value, callback) => {
              if (!value.length) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "",
            trigger: "change,blur"
        }]
      },
    };
  },
  created() {
    this.end = this.page_size;
    this.$api.get(
      "mygroupnum/",
      {
        status: "can_use",
      },
      (response) => {
        if (response.status != 200) {
          this.$Message.error("");
          this.$Message.error("" + response);
        } else {
          if (response.data.err_code == 0) {
            this.total = response.data.data;
          } else {
            this.$Message.error("" + response.data.error);
          }
        }
      }
    );
    this.getData();
  },
  methods: {
    change_check() {
      console.log(this.formItem.groups);
    },
    formatDate(UTCDateString) {
      if (!UTCDateString) {
        return "-";
      }
      function formatFunc(str) {
        //
        return str > 9 ? str : "0" + str;
      }
      var date2 = new Date(UTCDateString); //
      var year = date2.getFullYear();
      var mon = formatFunc(date2.getMonth() + 1);
      var day = formatFunc(date2.getDate());
      var hour = formatFunc(date2.getHours());
      var minute = formatFunc(date2.getMinutes());
      var second = formatFunc(date2.getSeconds());
      var dateStr =
        year + "-" + mon + "-" + day + " " + hour + ":" + minute + ":" + second;
      return dateStr;
    },
    getData() {
      this.$api.get(
        "mygroup/",
        {
          status: "can_use",
          start: (this.page - 1) * this.page_size + 1,
          end: this.page * this.page_size,
        },
        (response) => {
          if (response.status != 200) {
            this.$Message.error("");
            this.$Message.error("" + response);
          } else {
            if (response.data.err_code == 0) {
              this.list_data = response.data.data;
            } else {
              this.$Message.error("" + response.data.error);
            }
          }
        }
      );
    },
    submit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$api.post(
            "homework/",
            {
              name: this.formItem.name,
              type: this.formItem.type,
              subject: this.formItem.subject,
              remark: this.formItem.remark,
              end_time: this.formatDate(this.formItem.end_time),
              member_can_know_donelist: this.formItem.member_can_know_donelist,
              member_can_see_others: this.formItem.member_can_see_others,
              groups: this.formItem.groups,
            },
            (response) => {
              if (response.status != 200) {
                this.loading = false;
                this.$Message.error("");
                this.$Message.error("" + response);
              } else {
                var data = response.data;
                if (data.err_code != 0) {
                  this.$Message.error("" + data.error);
                  this.loading = false;
                } else {
                  this.$Message.success("");
                  this.loading = false;
                  var id = response.data.data.id;
                  this.$router.push("details/" + id);
                }
              }
            }
          );
        } else {
          this.$Message.error("");
        }
      });
    },
    change_page(page) {
      this.page = page;
      this.getData();
    },
  },
};
</script>

<style scoped>
</style>

