<template>
  <div id="new-group">
    <Layout>
      <Content>
        <Form ref="form" :model="formItem" :rules="rule">
          <FormItem label="" prop="name">
            <i-Input
              v-model="formItem.name"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem label="(" prop="password">
            <i-Input
              v-model="formItem.password"
              placeholder=""
            ></i-Input>
          </FormItem>

          <FormItem label=" " prop="member_can_use">
            <Tooltip max-width="300" content="">
              <Icon type="ios-help-circle" />
            </Tooltip>
            <i-switch v-model="formItem.member_can_use" />
          </FormItem>

          <FormItem label="">
            <i-Input
              type="textarea"
              :rows="5"
              v-model="formItem.desc"
              placeholder=""
            ></i-Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="submit()" :loading="loading"
              ></Button
            >
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
export default {
  name: "New",
  data() {
    return {
      loading: false,
      formItem: {
        name: "",
        desc: "",
        password: "",
        member_can_use: false,
      },
      rule: {
        name: [{ required: true, message: "", trigger: "change,blur" }],

        password: [
          { required: true, message: "", trigger: "change,blur" },
          {
            validator: (rule, value, callback) => {
              var reg = new RegExp("^[0-9a-zA-Z]{6,18}$");
              if (!reg.test(value)) {
                callback(true);
              } else {
                callback();
              }
            },
            message: "6-18",
            trigger: "change,blur",
          },
        ],
      },
    };
  },
  methods: {
    submit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$api.post(
            "group/",
            {
              name: this.formItem.name,
              desc: this.formItem.desc,
              password: this.formItem.password,
              member_can_use:this.formItem.member_can_use
            },
            (response) => {
              if (response.status != 200) {
                this.loading = false;
                this.$Message.error("");
                this.$Message.error("" + response);
              } else {
                var data = response.data;
                if (data.err_code != 0) {
                  this.$Message.error("," + data.error);
                  this.loading = false;
                } else {
                  this.$Message.success("");
                  this.loading = false;
                  var id = response.data.data.id;
                  this.$router.push("groupdetails/" + id);
                }
              }
            }
          );
        } else {
          this.$Message.error("");
        }
      });
    },
  },
};
</script>

<style scoped>
</style>

